.Layout {
    background: #fff;
}

.Header {
    padding: 0;
    box-shadow: 0px 0px 10px #ccc;
    z-index: 1000;
    position: fixed;
    width: 100%;
    height: auto;
    box-sizing: border-box;
    background: #fff;
}

.TopHeader {
    background: #455a64;
    padding: 0;
    line-height: 50px;
    height: 50px;
    width: 100%;
    padding: 4px;
    padding-left: 8px;
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
}

.BottomHeader > div {
    padding-top: 5px;
    padding-bottom: 5px;
}

.Main {
    background: #f0f2f5;
    margin-top: 100px;
}

.Button {
    color: #fff !important;
}

:global(.ant-menu-vertical) {
    border-right: none;
}

.MenuButtonContiner {
    flex: 1 1 auto;
    overflow: hidden;
    display: flex;
    align-items: center;
}

.QuickButtonContiner {
    flex: 0 0 auto;
    justify-content: flex-end;
    display: flex;
    align-items: center;
}
